body {
    font-family: "Myriad Pro", Arial, Helvetica, sans-serif;
}

h1 {
    margin: 0;
    text-align: center;
}

#container {
    text-align: center;
}

#grid-container {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: auto 1fr;
}

#scatter {
    grid-column: 2 / 3;
    grid-row: 1 / 3;
}

#legend {
    grid-column: 3 / 4;
    grid-row: 1 / 2;
    text-align: left;
}
#legend img {
    max-width: 75%;
}

#popup {
    grid-column: 3 / 4;
    grid-row: 2 / 3;
    text-align: left;
    font-size: 15px;
    line-height: .5;
}

circle {
    cursor: pointer;
}